import Vue from 'vue'
import Router from 'vue-router'
import Customers from '../components/Customers.vue'
import About from '../components/About.vue'
import Add from '../components/Add.vue'
Vue.use(Router)

//路由参数跳转
import CustomersDetails from '../components/CustomersDetails.vue'
import Edit from '../components/Edit.vue'


export default new Router({
  mode : "history",
  base : __dirname,
  routes: [
    {
      path : '/',
      component : Customers
    },
    {
      path : '/about',
      component : About
    },
    {
      path : '/add',
      component : Add
    },
    {
      path : '/customer/:id',
      component : CustomersDetails
    },
    {
      path : '/edit/:id',
      component : Edit
    },
    
  ]
})



