<template>
    <div class="add container">
        <alert v-if="alertMsg" :message="alertMsg"></alert>
        <form>
        <div class="well">
            <h4>用户信息</h4>
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name">
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email">
            </div>
            <div class="form-group">
                <label>学历</label>
                <input type="text" class="form-control" placeholder="education" v-model="customer.education">
            </div>
            <div class="form-group">
                <label>毕业学校</label>
                <input type="text" class="form-control" placeholder="graduationschool" v-model="customer.graduationschool">
            </div>
            <div class="form-group">
                <label>职业</label>
                <input type="text" class="form-control" placeholder="professional" v-model="customer.professional">
            </div>
            <div class="form-group">
                <label>经历</label>
                <textarea rows="10" class="form-control" v-model="customer.profile">

                </textarea>
                
            </div>
        </div>
        <input type="button" value="提交" @click="submitted" class="btn btn-promary">
        </form>
    </div>
</template>
<script>
import axios from 'axios'
import Alert from './Alert'
export default {
    name : "add",
    data () {
        return {
            customer : {},
            alertMsg : ""
        }
    },
    components: {
        Alert
    },
    methods : {
        submitted () {
            if(!this.customer.name || !this.customer.phone || !this.customer.email) {
                this.alertMsg = "请添加相关信息哦~"
            }else{
                axios.post("http://localhost:3000/users",this.customer)
                .then( res => {
                    this.$router.push({
                        //把数据push完之后，传一个值到customer里面去
                        path : '/',
                        query : {
                            alert : "用户信息添加成功",
                        }
                    });
                    // this.$emit("Onoff",true)
                })
            }
            
        }
    }
}
</script>
<style scoped>
h4{
    margin: 20px 0;
}
</style>



