<template>
    <div class="customersDetails container">
        <router-link to="/" class="btn btn-primary">返回</router-link>
        <h1 class="page-header">
            {{customer.name}}
            <span class="pull-right">
                <router-link :to="'/edit/' + id" class="btn btn-primary">编辑</router-link>
                <button class="btn btn-danger" @click="deleteCustomer(customer.id)">删除</button>
            </span>
        </h1>
        <form>
        <div class="well">
            <div class="form-group">
                <label>姓名</label>
                <input type="text" class="form-control" placeholder="name" v-model="customer.name">
            </div>
            <div class="form-group">
                <label>电话</label>
                <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
            </div>
            <div class="form-group">
                <label>邮箱</label>
                <input type="text" class="form-control" placeholder="email" v-model="customer.email">
            </div>
            <div class="form-group">
                <label>学历</label>
                <input type="text" class="form-control" placeholder="education" v-model="customer.education">
            </div>
            <div class="form-group">
                <label>毕业学校</label>
                <input type="text" class="form-control" placeholder="graduationschool" v-model="customer.graduationschool">
            </div>
            <div class="form-group">
                <label>职业</label>
                <input type="text" class="form-control" placeholder="professional" v-model="customer.professional">
            </div>
            <div class="form-group">
                <label>经历</label>
                <textarea rows="10" class="form-control" v-model="customer.profile">

                </textarea>
            </div>
        </div>
        </form>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name : "customersDetails",
    data () {
        return {
            customer : {},
            id : this.$route.params.id,
        }
    },
    created () {
        this.fetchCustomers(this.$route.params.id);
    },
    methods: {
        fetchCustomers (id) {
            axios.get("http://localhost:3000/users/" + id)
            .then( data => {
                this.customer = data.data;
            }); 
        },
        deleteCustomer (id) {
            axios.delete("http://localhost:3000/users/" + id)
                 .then( res => {
                     this.$router.push(
                        {
                         path : '/',
                         query : {
                             alert : "用户信息删除成功！" 
                         }
                        }
                    )
                 });
        }
    }

}
</script>
<style scoped>

</style>



