<template>
    <div class="customers container">
        <alert v-if="alertMsg" :message="alertMsg"></alert>
        <h1>用户管理系统</h1>
        <input type="text" class="form-control" placeholder="搜索" v-model="search">
        <table class="table table-striped">
            <thead>
                <tr>
                    <th>姓名</th>
                    <th>电话</th>
                    <th>邮箱</th>
                    <!-- <th>详情</th> -->
                </tr>
            </thead>
            <tbody>
                <tr v-for="customer in searchCustomers" :key="customer.id">
                    <td>{{customer.name}}</td>
                    <td>{{customer.phone}}</td>
                    <td>{{customer.email}}</td>
                    <td><router-link :to="'/customer/' + customer.id" class="btn">详情</router-link></td>
                </tr>
            </tbody>
        </table> 
    </div>
</template>
<script>
import axios from 'axios'
import Alert from './Alert'
export default {
    name : "customers",
    data  () {
        return {
            customers : [],
            alertMsg : "",
            search : ""
        }
    },
    computed: {
        searchCustomers () {
            return this.customers.filter( (customer) => {
                    return customer.name.match(this.search);
            })
        }
    },
    components: {
        Alert
    },
    created () {
        if(this.$route.query.alert) {
            this.alertMsg = this.$route.query.alert;
            console.log(this.alertMsg);
        };
        this.fetchCustomers();
        
    },
    updated () {
        this.fetchCustomers();
    },
    methods : {
        fetchCustomers () {
            axios.get("http://localhost:3000/users")
            .then( res => {
                this.customers = res.data;
            })
        },
        // searchCustomers () {
        //     return this.customers.filter( (customer) => {
        //         return customer.name.match(this.search);
        //     })
        // }
    }
}
</script>
<style scoped>
h1{
    font-size: 19px;
    margin-top: 20px 0;
}
</style>




